import { timecodeToSeconds } from "../timecode-converter";

/**
 * Converts Text format to DPE transcription format.
 * @param {string} textTranscript - Text format string to be converted.
 * @returns {Object} - DPE transcription object with paragraphs and words.
 */
function convertTxtToDpe(textTranscript) {
  const lines = textTranscript.split(/\n|<br>/);

  let dpeTranscription = {
    paragraphs: [],
    words: []
  };

  let startTime = 0;
  let endTime = 0;
  let timeCodeMatch = null;
  let timeCode = 0;
  let skip = textTranscript.includes('TRANSCRIPTION BEGIN'); // skip lines until TRANSCRIPTION BEGIN is detected

  lines.forEach((line, index) => {
    var timecodeRegex = /\[(?:[01]\d|2[0-3]):(?:[0-5]\d):(?:[0-5]\d)\]/g;
    var regex = /\b(?:\w+(?:-\w+)?(?:\s+\w+(?:-\w+)?)*):\s*/ig;
    timeCodeMatch = line.match(timecodeRegex);

    if(timeCodeMatch) {
      timeCode = timeCodeMatch[0].replace(']', '').replace('[', '')
      let matchedStartTime = timecodeToSeconds(timeCode);

      if (matchedStartTime !== startTime) {
        startTime = endTime;
      }
    }

    if (!skip && !timecodeRegex.test(line)) {
      let speaker = 'Add speaker'
      let speakerMatch = line.match(regex);

      if (speakerMatch) {
        speaker = speakerMatch[0].replace(':', '').trim()
      }

      let content = line;

      if (speakerMatch) {
        content = content.replace(speakerMatch[0], '');
      }

      content = content.trim();

      if (content) {
        // Split content into words and add to the words array
        const wordsArray = content && content.split(' ');
        let wordDuration = 0.5;

        // Add paragraph to DPE transcription
        let endTimeMatch = lines[index + 2]?.match(timecodeRegex)
        let endTimeCode = '';
        let matchedEndTime = null;
        let generatedEndTime = endTime;

        if(endTimeMatch) {
          endTimeCode = endTimeMatch[0].replace(']', '').replace('[', '')
          matchedEndTime = timecodeToSeconds(endTimeCode);
        }

        generatedEndTime = (wordsArray.length * wordDuration) + endTime;

        if (matchedEndTime === endTime) {
          matchedEndTime = generatedEndTime;
        }

        endTime = matchedEndTime || generatedEndTime

        dpeTranscription.paragraphs.push({ start: startTime, end: endTime, speaker: speaker });
        wordDuration = (endTime - startTime) / wordsArray.length;

        const words = wordsArray && wordsArray.map((word, wordIndex) => ({
          start: startTime + (wordIndex * wordDuration),
          end: startTime + ((wordIndex + 1) * wordDuration),
          text: word,
          speaker
        }));

        startTime = endTime
        dpeTranscription.words.push(...(words || []));
      }
    }
    if (skip) {
      skip = !line.includes('TRANSCRIPTION BEGIN');
    }
  });

  return dpeTranscription;
}

export { convertTxtToDpe };
